.header{
    &-contact{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        border-bottom: 1px solid #15151549;
        &-links1{
            a{
                font-family: Open Sans;
                font-style: normal;
                font-weight: normal;
                font-size: 12px;
                line-height: 16px;
                color: #151515;
                text-decoration: none;
                display: inline-block;
                padding: 17px;
                &:first-child{
                    color: #6A983C;
                    padding: 17px 17px 17px 0;
                }
            }
        }
        &-links2{
            a{
                font-family: Open Sans;
                font-style: normal;
                font-weight: normal;
                font-size: 12px;
                line-height: 16px;
                text-decoration: none;
                display: inline-block;
                padding: 17px;
                color: #6A983C;
                &:last-child{
                    padding: 17px 0px 17px 17px;
                }
            }
        }
        
    }

    &-center{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 40px 0px;
        &_logo{
            width: 177px;
            height: 18px;
            object-fit: contain;
            cursor: pointer;
        }
        &-search{
            display: flex;
            justify-content: space-between;
            align-items: center;
            border: 1px solid #D1D1D1;
            border-radius: 12px;
            & select{
                padding: 12px 22px 12px 16px;
                background-color: transparent;
                border: none;
                position: relative;
                display: block;
                appearance: none;
                background-image: url('../../assets/source/downgreen.svg');
                background-repeat: no-repeat;
                background-position: 95% center; 
                &::after{
                    content: "";
                    display: block;
                    width: 10px;
                    height: 10px;
                    background-color:red;
                    z-index: 3;
                }
            }
            & input{
                padding: 12px 44px 12px 24px;
                width: 300px;
                background-color: transparent;
                border: none;
                appearance: none;
            }
        }
        &-authory{
            display: flex;
            justify-content: space-between;
            align-items: center;
            button{
                padding: 10px 25px;
                background: transparent;
                border: 1px solid transparent;
                &:nth-child(2){
                    position: relative;
                    &:before{
                        content: "4";
                        position: absolute;
                        padding: 0px 4px;
                        top: 55%;
                        left: 25%;
                        font-size: 9px;
                        background-color: rgb(253, 68, 68);
                        border-radius: 50%;
                        color: #fff;
                    }
                }
            }
            img{
                object-fit: contain;
                width: 18px;
                height: 20px;
            }
        }
    }

    &-nav{
        width: 100%;
        background-color:#F9F9F9;
        &-inner{
            a{
                color: #151515;
                text-decoration: none;
                display: inline-block;
                padding: 16px 27px;
                font-style: normal;
                font-weight: 500;
                font-size: 15px;
                line-height: 22px;
                position: relative;
                &:first-child{
                    padding: 16px 27px 16px 0px;
                }
                &:after{
                    content: "";
                    position: absolute;
                    top: 40%;
                    right: 0;
                    width: 13px;
                    height: 13px;
                    background-image: url('../../assets/source/Vector.svg');
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: contain;
                }
            }
        }
    }

}




.container-fluid::after, .container2::after, .container::after, aside::after, footer::after, header::after, section::after{
    display: none;
}


.slider .backside{
    border-radius: 5px;
}