.card{
    max-width: 280px;
    padding: 16px;
    border-radius: 12px;
    border: 1px solid #D1D1D1;
    display: inline-block;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    margin: 0;
    &-img{
        width: 100%;
        max-height: 250px;
        border-radius: 12px;
        object: contain;
        margin-bottom: 25px;
    }
    &-title{
        color: #151515;
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        margin-bottom: 10px;
    }
    &-description{
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        color: #575757;
        margin-bottom: 16px;
    }
    &-price{
        display: flex;
        justify-content: space-between;
        align-items: center;
        p{
            color: #151515;
            font-style: normal;
            font-weight: 600;
            font-size: 18px;
        }
        button{
            padding: 6.5px 12px;
            background: #6A983C;
            color: #FFFFFF;
            font-style: normal;
            font-weight: bold;
            font-size: 15px;
            border: 2px solid #46760A;
            border-radius: 12px;
            cursor: pointer;
        }
    }

}