*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Poppins', sans-serif;
}

.container{
    max-width: 1400px;
    width: 100%;
    margin: 0 auto;
}

.myCheck::before{
    border-color: white !important;
}

.checkbox input[type=checkbox]~.check{
    border-radius: 3px;
    border-color: #d1d1d1;
}

.bd-green2{
    border-color: #d1d1d1;
}

.checkbox input[type=checkbox]:checked~.check{
    background-color: #6A983C;
    border-radius: 3px;
    border-color: #46760A;
}
