.footer{
    margin-top: 80px;
    &-top{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 50px;
        &-nav{
            display: flex;
            flex-direction: column;
            h3{
                margin-bottom: 10px;
            }
            a{
                text-decoration: none;
                display: inline-block;
                padding: 8px 0px;
                color: #6A983C;
            }
        }
    }
    &-center{
        margin-bottom: 50px;
        h3{
            color: #151515;
            margin-bottom: 20px;
        }
        &-link{
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-wrap: wrap;
            a{
                display: inline-block;
                padding: 4px 10px;
                background: #F5F5F5;
                border-radius: 12px;
                color: #151515;
                text-decoration: none;
                margin-right: 16px;
                margin-bottom: 16px;
            }
        }
    }
    &-footer{
        color: #151515;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
    }
}