.blogCard{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 280px;
    text-decoration: none;
    color: #151515;
    margin-bottom: 40px;
    img{
        width: 100%;
        object-fit: cover;
        margin-bottom: 25px;
    }
    &_tag{
        display: inline-block;
        padding: 4px 12px;
        border-radius: 12px;
        background: #F4F8EC;
        font-weight: 600;
        font-size: 12px;
        color: #6A983C;
        margin-bottom: 10px;
    }
    h3{
        font-weight: 600;
        font-size: 18px;
    }
    &-author{
        width: 60%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}