.fruitsingle{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top: 30px;
    &-imgside{
        width:40%;
        img{
            width: 570px;
        display: block;
        height: 440px;
        margin-bottom: 40px;
        object-fit: cover;
        border-radius: 12px;
        }
    }
    
    &-info{
        width: 57%;
        &_title{
            font-weight: 600;
            font-size: 32px;
            margin-bottom: 10px;
        }
        &-rate{
            display: flex;
            align-items: center;
            margin-bottom: 40px;
            img{
                width: 17px;
                height: 17px;
            }
            span{
                display: inline-block;
                padding: 0px 9px;
                font-size: 12px;
                line-height: 16px;
                color: #A9A9A9;
                text-decoration: underline;
            }
        }
        &_text{
            font-weight: normal;
            font-size: 17px;
            margin-bottom: 40px;
        }
        &-book{
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 90%;
            margin-bottom: 40px;
            &-price{
                h1{
                    font-weight: 600;
                    font-size: 26px;
                    color: #6A983C;
                }
                p{
                    color: #A9A9A9;
                    font-weight: 600;
                    font-size: 12px;
                }
            }
            &-option{
                max-width: 120px;
                width: 100%;
                display: flex;
                justify-content: space-between;
                background: #F9F9F9;
                border: 1px solid #D1D1D1;
                border-radius: 12px;
                padding: 14px 20px 14px 16px;
                input{
                    width: 30%;
                    border: none;
                    background: transparent;
                    border-right: 1px solid #D1D1D1;
                }
                select{
                    width: 70%;
                    border: none;
                    background: transparent;
                    padding: 0 5px;
                }
            }
            button{
                padding: 12px 16px 12px 36px;
                background: #6A983C;
                border: 2px solid #46760A;
                border-radius: 12px;
                color: #FFFFFF;
                font-weight: bold;
                font-size: 15px;
                background-image: url('../../assets/source/fruitsingle/plus.svg');
                background-repeat: no-repeat;
                background-size: 11px 11px contain;
                background-position: 10% 50%; 
            }
        }
        &-favourite{
            display: flex;
            button{
                padding: 6px 12px 6px 34px;
                background-color: transparent;
                border: none;
                font-weight: bold;
                font-size: 15px;
                margin-right: 10px;
                &:first-child{
                    background-image: url('../../assets/source/fruitsingle/lover.svg');
                    background-repeat: no-repeat;
                    background-size: 11px 11px contain;
                    background-position: 5% 50%; 
                }

                &:nth-child(2){
                    background-image: url('../../assets/source/fruitsingle/compare.svg');
                    background-repeat: no-repeat;
                    background-size: 11px 11px contain;
                    background-position: 5% 50%; 
                }
            }
        }
        &-descript{
            &-nav{
                border-bottom: 1px solid #F5F5F5;
                margin-bottom: 25px;
                a{
                    padding: 16px 25px;
                    display: inline-block;
                    color: #151515;
                    postition: relative;
                    border-bottom: 2px solid transparent;
                    &:hover{
                        text-decoration:none;
                        border-bottom: 2px solid green;
                        color: green;
                    }
                }
            }
        }
    }
    &-bottom{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        &-advice{
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            margin-bottom: 25px;
            button{
                padding: 6px 34px 6px 16px;
                background-color: transparent;
                border: none;
                background: url('../../assets/source/greenright.svg');
                background-repeat: no-repeat;
                background-size: 13px 13px contain;
                background-position: 90% 50%;
                }
        }
        &-card{
            // border: 1px;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            width: 100%;
        }
    }
}


table{
    width: 100%;
    margin-bottom: 40px;
    th{
        color: #A9A9A9;  
        padding: 5px 0;  
    }
    td{
        font-weight: normal;
        padding: 5px 0;  
        font-size: 14px;
    }
    tr{
        margin-bottom: 10px;
    }
}
