.mainblog{
    max-width: 47%;
    width: 100%;
    padding: 24px;
    background-image: url('../../assets/source/Blog/Rectangle\ 26.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 12px;
    text-decoration: none;
    &_tag{
        padding: 2px 10px;
        color: #6A983C;
        background: #F4F8EC;
        border-radius: 12px;
        display: inline-block;
        margin-bottom: 200px;
    }
    &_title{
        color: #FFFFFF;
        font-weight: 600;
        font-size: 22px;
    }
    &-info{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 30%;
        img{
            width: 32px;
            height: 32px;
            border-radius: 50%;
            object-fit: cover;
        }
        span{
            font-weight: normal;
            font-size: 12px;
            color: #fff;
        }
    }
}