.fruitpage{
    &-settings{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: center;
        width: 100%;
        margin-bottom: 24px;
        margin-top: 40px;
        &-btn{
            button{
                padding: 20px 30px 20px 35px;
                background-color: transparent;
                border: 1px solid transparent;
                &:nth-child(1){
                    position: relative;
                    &:before{
                        content: "";
                        position: absolute;
                        top: 38.5%;
                        left: 10%;
                        background-image: url('../../assets/source/grid.svg');
                        background-repeat: no-repeat;
                        background-position: center;
                        background-size:cover;
                        width: 15px;
                        height: 15px;
                    }
                }
                &:nth-child(2){
                    position: relative;
                    &:before{
                        content: "";
                        position: absolute;
                        top: 38.5%;
                        left: 10%;
                        background-image: url('../../assets/source/fruitpage/listview.svg');
                        background-repeat: no-repeat;
                        background-position: center;
                        background-size:cover;
                        width: 15px;
                        height: 15px;
                    }
                }
                &:nth-child(3){
                    position: relative;
                    &:before{
                        content: "117";
                        background: #F4F8EC;
                        color: #6A983C;
                        padding: 4px 12px;
                        border-radius: 12px;
                        margin-right: 10px;
                    }
                }
            }
        }
    }



    &-customize {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 25px;
        &-radio{
            background: #F9F9F9;
            padding: 13px 18px;
            border-radius: 12px;
            border: 1px solid #D1D1D1;
            margin-right: 25px;
        }
        &-checkbox{
            padding: 11px 18px;
            background: #F9F9F9;
            border: 1px solid #D1D1D1;
            border-radius: 12px;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            min-width: 151px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-right: 25px;

            input[type="checkbox"]{
                width: 20px;
                height: 20px;
            }
            &-nbm{
                border: 1px solid #EBEBEB;
                border-radius: 12px;
                padding: 2px 8px 4px;
                margin-left: 5px;
                span{
                    font-weight: 600;
                    font-size: 12px;
                }
            }
            select{
                padding: 8px 12px;
                border: none;
                background-color: transparent;
                border-left: 1px solid #D1D1D1;
                font-weight: 600;
                font-size: 12px;
            }
        }

    }



    &-filtres{
        span{
            font-weight: 600;
            font-size: 12px;
            line-height: 18px;
            color: #A9A9A9;
            display: inline-block;
            margin-right: 25px;
        }

        button{
            font-weight: 600;
            font-size: 12px;
            line-height: 18px;
            color: #6A983C;
            background: #F4F8EC;
            border-radius: 12px;
            border: 1px solid transparent;
            padding: 0px 24px 0px 8px;
            background-image: url('../../assets/source/fruitpage/ic-actions-close-simple.svg');
            background-repeat: no-repeat;
            background-position: 90% center;
            margin: 0px 10px;
        }
    }


    &-main{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding: 64px 0px;
        &-nav{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            width: 25%;
            &-categories{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                width: 100%;
                margin-bottom: 50px;
                h1{
                    font-style: normal;
                    font-weight: 600;
                    font-size: 18px;
                }
                div{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    width: 100%;
                    padding: 6px 0px;
                    h2{
                        font-weight: normal;
                        font-size: 14px;
                        margin-bottom: 0;
                    }
                    p{
                        font-weight: 600;
                        font-size: 12px;
                        color: #6A983C;
                        margin-bottom: 0;
                        padding: 0px 8px;
                        background: #F4F8EC;
                        border-radius: 12px;
                    }
                }
            }
            &-brands{
                display: flex;
                flex-direction: column;
                font-weight: normal;
                font-size: 14px;
                margin-bottom: 50px;
                h1{
                    font-weight: 600;
                    font-size: 18px;
                }
                label{
                    display: flex;
                    align-items: center;
                    padding: 8px 0px;
                    input[type="checkbox"]{
                        width:20px;
                        height:20px;
                    }
                    img{
                        margin: 0px 2px;
                    }
                }
            }
            &-price{
                display: flex;
                flex-direction: column;
                width: 100%;
                h1{
                    font-weight: 600;
                    font-size: 18px;
                    margin-bottom: 25px;
                }
                &-minmax{
                    margin-top: 25px;
                    margin-bottom: 25px;
                    width: 100%;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    span{
                        display: inline-block;
                        width: 15px;
                        height: 2px;
                        margin-top: 15px;
                        background-color: #D1D1D1;
                    }
                    label{
                        display: flex;
                        flex-direction: column;
                        font-weight: 600;
                        font-size: 12px;
                        input{
                            background: #F9F9F9;
                            border: 1px solid #D1D1D1;
                            border-radius: 12px;
                            width: 110px;
                            padding: 12px 16px 12px 22px;
                            appearance: none;
                        }
                    }
                }
                &-button{
                    display: flex;
                    justify-content: flex-start;
                    button{
                        padding: 12.5px 16px;
                        border-radius: 12px;
                        margin-right: 50px;
                        &:nth-child(1){
                            background: #6A983C;
                            border: 2px solid #46760A;
                            color: #FFFFFF;
                            font-weight: bold;
                            font-size: 15px;
                        }
                        &:nth-child(2){
                            background: transparent;
                            border: 2px solid transparent;
                            color: #A9A9A9;
                            font-weight: bold;
                            font-size: 15px;
                            margin-right: 50px;
                            &:hover{
                                border: 2px solid #A9A9A9;
                            }
                        }
                    }
                }
            }
        }
    }

    &-more{
        display: flex;
        justify-content: space-between;
        align-items: center;
        &-pagination{
            display: flex;
            justify-content: space-between;
            align-items: center;
            span{
                color: #A9A9A9;
                display: inline-block;
            }
            a{
                padding: 4px;
                display: inline-block;
                color: black;
                &:hover{
                    color: green;
                    text-decoration: none;

                }
            }
        }
        button{
            padding: 12px 36px 12px 16px;
            background: #6A983C;
            border: 2px solid #46760A;
            border-radius: 12px;
            color: #FFFFFF;
            font-weight: bold;
            font-size: 15px;
            background-image: url('../../assets/source/fruitpage/downIndex.svg');
            background-repeat: no-repeat;
            background-size: 30px 30px contain;
            background-position: 92% 52%;
        }
        &-result{
            span{
                &:first-child{
                    color: #6A983C;
                    display: inline-block;
                    background: #F4F8EC;
                    border-radius: 12px;
                    font-weight: 600;
                    font-size: 12px;
                    padding: 4px 8px;
                }
                &:nth-child(2){
                    color: #A9A9A9;
                    font-weight: normal;
                    padding: 4px 8px;
                    font-size: 12px;
                }
            }
        }
    }

}


.gridlist{
    width: 70%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
}





















/* The fruitpage-customize-radio-label */
.fruitpage-customize-radio-label {
    justify-content: space-between;
    position: relative;
    padding-left: 35px;
    cursor: pointer;
    margin: 0px 17px;
    font-weight: normal;
    font-size: 14px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  
  /* Hide the browser's default radio button */
  .fruitpage-customize-radio-label input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
  }
  
  /* Create a custom radio button */
  .checkmark {
    position: absolute;
    top: 8%;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: #eee;
    border-radius: 50%;
  }
  
  /* On mouse-over, add a grey background color */
  .fruitpage-customize-radio-label:hover input ~ .checkmark {
    background-color: #ccc;
  }
  
  /* When the radio button is checked, add a blue background */
  .fruitpage-customize-radio-label input:checked ~ .checkmark {
    background-color: #151515;
  }
  
  /* Create the indicator (the dot/circle - hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the indicator (dot/circle) when checked */
  .fruitpage-customize-radio-label input:checked ~ .checkmark:after {
    display: block;
  }
  
  /* Style the indicator (dot/circle) */
  .fruitpage-customize-radio-label .checkmark:after {
        top: 30%;
      left: 30%;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: white;
  }


















