.menu{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    h3{
        margin-bottom: 10px;
    }
    a{
        color: #6A983C;
        display: inline-block;
        padding: 6px 0;
    }
    button{
        padding: 12px 38px 12px 16px;
        margin-top: 20px;
        font-style: normal;
        font-weight: bold;
        font-size: 15px;
        line-height: 22px;
        background: #F5F5F5;
        border-radius: 12px;
        border: 1px solid transparent;
        position: relative;
        &:after{
            content: "";
            position: absolute;
            top: 40%;
            right: 10%;
            width: 10px;
            height: 10px;
            background-image: url('../../assets/source/right.svg');
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
        }
    }
}

.section1{
    display: flex;
    margin-top: 40px;
    justify-content: space-between;
    padding: 40px 0px;
    margin-bottom: 50px;
    &-card{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        padding: 43px 33px;
        max-width: 475px;
        background-color: rgb(0, 128, 0);
        background: url('../../assets/source/S1background.svg'), #F4F8EC;
        width: 100%;
        border-radius: 12px;
        h5{
            color: #6A983C;
        }
        h3{
            color: #151515;
        }
        a{
            display: inline-block;
            padding: 12px 38px 12px 16px;
            border: 2px solid #92C064;
            border-radius: 12px;
            text-decoration: none;
            position: relative;
            font-style: normal;
            font-weight: bold;
            font-size: 15px;
            line-height: 22px;
            color: #151515;
            &:after{
                content: "";
                position: absolute;
                top: 40%;
                right: 10%;
                width: 10px;
                height: 10px;
                background-image: url('../../assets/source/greenright.svg');
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
            }
        }
    }
}


.carusel{
    width: 100%;
    height: 400px;
    position: relative;
    overflow: hidden;
    margin-top: 50px;
    &-span{
        position: absolute;
        top: 30%;
        font-weight: bold;
        left: 0%;
        cursor: pointer;
        z-index: 1;
        background: #000;
        padding: 13px 17px;
        &:nth-child(2){
             left: 96%;
             text-align: right;
        }
        img{
            width:25px;
            height:25px;
            
        }
    }
    section{
        width:98%;
        height:300px;
        background-color:#f4f4f4;
        margin: 10px auto;
        display: flex;
        justify-content: left;
        overflow-x: auto;
        &::-webkit-scrollbar{
            visibility: hidden;
        }
        div{
            min-width: 330px;
            height: 200px;
            position: relative;
            left: 0;
            text-align:center;
            padding-top:60px;
            font-size:100px;
            font-weight:bold;
            margin: 10px 20px;
            color: white;
            background-color:purple;
            transition: all ease 1s;
        }
    }

}

.homepage{
    h1{
        font-weight: 600;
        font-size: 18px;
    }
    &-comments{
        width:100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px 0;
        a{
            display: inline-block;
            padding: 12px 38px 12px 16px;
            border-radius: 12px;
            text-decoration: none;
            position: relative;
            font-style: normal;
            font-weight: bold;
            font-size: 15px;
            line-height: 22px;
            color: #151515;
            &:after{
                content: "";
                position: absolute;
                top: 40%;
                right: 10%;
                width: 10px;
                height: 10px;
                background-image: url('../../assets/source/right.svg');
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
            }
        }
    }
    &-headline{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 100px;
    }
    &-blogpage{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 100px;
        &-card1{
            max-width: 470px;
            width: 100%;
            border-radius: 12px;
            padding: 24px;
            display: inline-block;
            text-decoration: none;
            &-p{
                color: #6A983C;
                font-style: normal;
                font-weight: 600;
                font-size: 12px;
                padding: 5px 10px;
                border-radius:12px;
                background: #F4F8EC;
                display: inline-block;
                margin-bottom: 230px;
            }
            background-image: url('../../assets/source/egg.jpg');
            background-size: cover;
            h4{
                color: #FFFFFF;
                font-style: normal;
                font-weight: 600;
                font-size: 22px;
                margin-bottom: 20px;
            }
            div{
                display: flex;
                align-items: center;
                color: #FFFFFF;
                img{
                    width: 45px;
                    height: 45px;
                    border-radius:50%;
                    object-fit: cover;
                }
                span{
                    display: inline-block;
                    margin: 0px 25px;
                }
            }
        }
        &-card2{
            max-width: 270px;
            width: 100%;
            text-align: left;
            text-decoration: none;
            color: #333;
            img{
                width: 100%;
                margin-bottom: 25px;
            }
            p{
                color: #6A983C;
                font-style: normal;
                font-weight: 600;
                font-size: 12px;
                padding: 5px 10px;
                border-radius:12px;
                background: #F4F8EC;
                display: inline-block;
                margin-bottom: 10px;
            }
            h3{
                font-weight: 600;
                font-size: 18px;
                margin-bottom: 16px;
            }
            span{
                display: inline-block;
                &:nth-child(2){
                    margin: 0 25px;
                }
            }
        }
        &-card3{
            display: flex;
            flex-direction: column;
            &-card{
                display: flex;
                justify-content: space-between;
                align-items: flex-start;
                margin-bottom: 35px;
                width:480px;
                text-decoration: none;
                color: #333;
                &-text{
                    p{
                        font-weight: 500;
                        font-size: 15px;
                        max-width: 225px;
                        margin-bottom: 20px;
                    }
                    span{
                        display: inline-block;
                        &:nth-child(2){
                            margin: 0 25px;
                        }
                    }
                }
                img{
                    width: 100px;
                    height: 100px;
                    object-fit: cover;
                    border-radius: 12px;
                }
            }
        }
    }
}
