.list{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    height: 280px;
    border-radius: 12px;
    border: 1px solid #D1D1D1;
    width: 100%;
    text-decoration: none;
    margin-bottom: 32px;
    color: #151515;
    &_img{
        width:270px;
        height: 100%;
        object-fit: cover;
        border-radius: 12px 0px 0px 12px;
    }
    h1{
        font-weight: 600;
        font-size: 18px;
    }
    &_descript{
        font-weight: normal;
        font-size: 12px;
        color: #575757;
        margin-bottom: 10px;
    }
    &-stars{
        margin-bottom: 25px;
    }
    &-description{
        max-width:250px;
        padding: 15px 0;
        p{
            width:100%;
            display: flex;
            justify-content: space-between;
            color: #A9A9A9;
            font-weight: normal;
            font-size: 14px;
            margin-bottom: 10px;
            text-align: left;
            &:last-child{
                span{
                    color: #6A983C;
                }
            }
        }
    }
    &-price{
        width:260px;
        padding: 15px 0px;
        h1{
            font-weight: 600;
            font-size: 18px;
        }
        &_p{
            color: #A9A9A9;
            font-weight: 600;
            margin-bottom: 10px;
            &:last-child{
                margin-bottom: 0;
            }
        }
        span{
            font-weight: normal;
            font-size: 12px;
            color: #A9A9A9;
            display: block;
            margin-bottom: 20px;
        }
        &-deteil{
            background: #6A983C;
            color: #FFFFFF;
            padding: 12px 36px 12px 16px;
            border-radius: 12px;
            text-align: left;
            display: inline-block;
            font-weight: bold;
            font-size: 15px;
            border: 2px solid transparent;
            margin-bottom: 12px;
            background-image: url('../../assets/source/fruitpage/whiteright.svg');
            background-repeat: no-repeat;
            background-position: 90% center;
        }
        &-add{
            border: 2px solid transparent;
            padding: 6.5px 12px 6.5px 36px;
            font-style: normal;
            font-weight: bold;
            background: #F5F5F5;
            border-radius: 12px;
            background-image: url('../../assets/source/fruitpage/blackheart.svg');
            background-repeat: no-repeat;
            background-position: 10% center;
        }
    }
}