.blog{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top: 60px;
    &-main{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 40px;
    }
    &-header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 40px;
        width: 100%;
        h1{
            font-weight: 600;
            font-size: 32px;
        }
        &-view{
            button{
                padding: 14px 20px;
                background: transparent;
                border: 1px solid transparent;
                background-color: transparent;
                color: #A9A9A9;
                &:hover{
                    color: #151515;
                }
                &:first-child{
                    position: relative;
                    &:before{
                        content: "";
                        position: absolute;
                        top: 35%;
                        left: 0;
                        width: 15px;
                        height: 15px;
                        background-repeat: no-repeat;
                        background-image: url('../../assets/source/grid.svg');
                        background-repeat: no-repeat;

                    }
                }

                &:nth-child(2){
                    position: relative;
                    &:before{
                        content: "";
                        position: absolute;
                        top: 35%;
                        left: 0;
                        width: 15px;
                        height: 15px;
                        background-image: url('../../assets/source/list.svg');
                        background-repeat: no-repeat;
                    }
                }

                &:nth-child(3){
                    position: relative;
                    &:before{
                        content: "117";
                        padding: 5px 8px;
                        margin-right: 5px;
                        color: #6A983C;
                        background: #F4F8EC;
                        border-radius: 12px;
                    }
                }


            }
        }
    }
    &-nav{
        width: 20%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 40px;
        &-category{
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 40px;
            h3{
                font-weight: 600;
                font-size: 18px;
            }
            a{
                color: #6A983C;
                padding: 6px 0px;
                display: inline-block;
                &:hover{
                    color: #A9A9A9;
                }   
            }
        }
        &-text{
            h3{
                font-weight: 600;
                font-size: 18px;
            }
            p{
                color: #A9A9A9;
                font-weight: normal;
                font-size: 14px;
                margin-bottom: 20px;
            }
            &-input{
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: flex-start;
                background: #F9F9F9;
                background: #F9F9F9;
                border: 1px solid #D1D1D1;
                border-radius: 12px;
                
                input{
                    width: 70%;
                    background-color: transparent;
                    border: 1px solid transparent;
                    padding: 12px 4px 12px 22px;
                }
                button{
                    font-weight: bold;
                    font-size: 15px;
                    padding: 12px 16px 12px 4px;
                    border-radius: 0px 12px 12px 0px;
                    border: 1px solid transparent;
                    background-color: transparent;
                }
            }
        }
    }
    &-cards{
        width: 78%;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: flex-start;
    }


    &-pagination{
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        background: #FDFDFD;
        padding: 10px 0px;
        margin: 20px 0px;
        &-numbers{
            a{
                font-weight: normal;
                font-size: 12px;
                font-weight: normal;
                font-size: 12px;
                display: inline-block;
                padding: 0px 6px;
                &:hover{
                    color: #6A983C;
                    text-decoration: none;
                }
            }
            span{
                color: #A9A9A9;
            }
        }
        button{
            background: #6A983C;
            padding: 12px 38px 12px 16px;
            border: 2px solid #46760A;
            border-radius: 12px;
            font-weight: bold;
            font-size: 15px;
            color: #fff;
            position: relative;
            &:before{
                content: "";
                position: absolute;
                top: 43%;
                right: 10px;
                width: 10px;
                height: 10px;
                background-image: url('../../assets/source/Blog/fffright.svg');
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
        p{
            color: #A9A9A9;
            position: relative;
            margin-bottom: 0;
            &:before{
                content: '198';
                color: #6A983C;
                margin-right: 10px;
                padding: 4px 12px;
                background: #F4F8EC;
                border-radius: 12px;
            }
        }
    }
}