.CardCarusel{
    max-width: 420px;
    text-align: center;
    padding: 24px 32px;
    border: 1px solid #F1F1F1;
    border-radius: 12px;
    margin-right: 30px;
    h5{
        color: #151515;
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        margin-bottom: 24px;
    }
    p{
        color: #A9A9A9;
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        margin-bottom: 24px;
    }
    img{
        width: 48px;
        height: 48px;
        border-radius: 50%;
        z-index: 1000000000000000;
    }
    &:last-child{
        margin-right: 1;
    }
}
