.descripttable{
    tr{
        padding: 11px 0px;
        &:nth-child(even){
            background-color: #f2f2f2;
        }
        &:first-child{
            border-bottom: 2px solid #EBEBEB;
        }
        th{
            padding: 11px 0px;
            font-weight: 600;
            color: #151515;
            font-size: 12px;
        }
        td{
            padding: 11px 0px;
            font-weight: normal;
            font-size: 14px;
        }
    }

}

