.payment{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 60px;
    &-info{
        width: 60%;
        &_title{
            font-weight: 600;
            font-size: 22px;
            margin-bottom: 5px;
        }
        &-step{
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 35px;
            p{
                color: #A9A9A9;
                font-weight: normal;
                font-size: 12px;
            }
        }
        &-data{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: flex-start;
            label{
                display: block;
                color: #151515;
                font-weight: 600;
                font-size: 12px;
            }
            input{
                width: 100%;
                border-radius: 12px;
                padding: 12px 16px 12px 22px;
                background: #F9F9F9;
                border: 1px solid #D1D1D1;
                margin-bottom: 35px;
            }
            div{
                width: 45%;
            }
        }
        &-check{
            width: 40%;
            background: #F9F9F9;
            border-radius: 12px;
            padding: 12px 16px 12px 22px;
            margin-bottom: 65px;
            display: flex;
            align-items: center;
            label{
                padding-left: 10px;
            }
        }
        &-choosingMethod{
            border-radius: 12px;
            background: #F9F9F9;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 20px;
            &-text{
                display: flex;
                align-items: center;
                p{
                    margin-top: 5px;
                    font-weight: 600;
                    font-size: 12px;
                    margin: 0;
                }
                span{
                    color: #6A983C;
                    font-weight: 600;
                    padding: 0 10px;
                    margin-bottom: 0;
                    font-weight: 600;
                    font-size: 12px;
                }
            }
            &-inputs{
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 14px 18px;
                input{
                    display: inline-block;
                }
                label{
                    font-weight: 600;
                    font-size: 12px;
                }
            }
            img{
                object-fit: contain;
                padding: 14px 18px;
            }
        }
        &-creditCard{
            input{
                width: 100%;
                border-radius: 12px;
                padding: 12px 16px 12px 22px;
                background: #F9F9F9;
                border: 1px solid #D1D1D1;
            }
            label{
                font-weight: 600;
                font-size: 12px;
                margin-bottom: 10px;
            }
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            padding: 18px;
            border: 1px solid #D1D1D1;
            border-radius: 12px;
            margin-bottom: 35px;
            &-header{
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 20px;
                &-input{
                    display: flex;
                    align-items: center;
                    font-size: 12px;
                }
                img{
                    &:first-child{
                        padding: 0px 10px;
                    }
                }
            }
            &-cardNumber{
                display: flex;
                flex-direction:column;
                width: 100%;
                margin-bottom: 35px;
            }
            &-deteails{
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 100%;
                margin: 0;
                div{
                    display: flex;
                    flex-direction: column;
                }
                &-cardHolder{
                    width: 45%;
                }
                &-data{
                    width: 25%;
                    input{
                        color: #A9A9A9;
                        text-transform: uppercase;
                    }
                }
                &-cvc{
                    width: 25%;
                    input{
                        button{
                            display: none;
                        }
                    }
                }
            }
        }

        &-textarea{
            display: flex;
            flex-direction: column;
            margin-bottom: 80px;
            label{
                font-weight: 600;
                font-size: 12px;
                margin-bottom: 10px;
            }
            textarea{
                resize: none;
                max-height: 120px;
                width: 100%;
                border-radius: 12px;
                padding: 12px 16px 12px 22px;
                background: #F9F9F9;
                border: 1px solid #D1D1D1;
            }
        }
        &-agreement{
            display: flex;
            align-items: center;
                width: 100%;
                border-radius: 12px;
                padding: 12px 16px 12px 22px;
                background: #F9F9F9;
                border: 1px solid #D1D1D1;
                margin-bottom: 25px;
        }
    }
    &-submit{
        font-weight: bold;
        font-size: 15px;
        padding: 16.5px 48px;
        color: #FFFFFF;
        border: 2px solid #46760A;
        background: #6A983C;
        border-radius: 12px;
    }


    &-order{
        width: 38%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        border: 1px solid #D1D1D1;
        padding: 32px 16px;
        border-radius: 12px;
        &_title{
            font-weight: 600;
            font-size: 22px;
        }
        &_alert{
            font-weight: normal;
            font-size: 12px;
            color: #A9A9A9;
        }
        &-card{
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            width: 100%;
            border-bottom: 1px solid #F9F9F9;
            margin-bottom: 40px;
            &-show{
                display: flex;
                flex-direction: column;
                align-items: flex-start;    
                width: 35%;
                
                img{
                    width: 100%;
                    height: 100px;
                    border-radius: 12px;
                    object-fit: cover;
                    margin-bottom: 15px;
                }
                button{
                    color: #A9A9A9;
                    padding: 5px 20px;
                    background-color: transparent;
                    border: 1px solid transparent;
                    cursor: pointer;
                    &:nth-child(2){
                        position: relative;
                        &:before{
                            content: '';
                            position: absolute;
                            width: 15px;
                            height: 12px;
                            top: 30%;
                            left: 0px;
                            background-image: url('../../assets/source/cardForm/heart.svg');
                            background-repeat: no-repeat;
                            background-size: contain;
                        }
                    }

                    &:nth-child(3){
                        position: relative;
                        &:before{
                            content: '';
                            position: absolute;
                            width: 15px;
                            height: 12px;
                            top: 30%;
                            left: 0px;
                            background-image: url('../../assets/source/cardForm/compare.svg');
                            background-repeat: no-repeat;
                            background-size: contain;
                        }
                    }

                    &:nth-child(4){
                        position: relative;
                        &:before{
                            content: '';
                            position: absolute;
                            width: 15px;
                            height: 12px;
                            top: 30%;
                            left: 0px;
                            background-image: url('../../assets/source/cardForm/remove.svg');
                            background-repeat: no-repeat;
                            background-size: contain;
                        }
                    }
                }
            }

            &-info{
                width: 60%;
                &_title{
                    font-weight: 500;
                    font-size: 15px;
                }
                &-table{
                    margin-bottom: 0;
                    th{
                        font-weight: normal;
                        font-size: 12px;
                    }
                    td{
                        font-weight: normal;
                        font-size: 12px;
                    }
                }
                &-stars{
                    margin-bottom: 20px;
                    img{
                        object-fit: cover;
                    }
                }
                &-price{
                    display: flex;
                    justify-content: space-between;
                    align-items:flex-start;
                    &-cost{
                        width: 150px;
                        h3{
                            font-weight: 600;
                            font-size: 18px;
                            color: #6A983C;
                        }
                        p{
                            color: #A9A9A9;
                            font-weight: 600;
                        }
                    }
                    &-select{
                        padding: 7px 12px 7px 9px;
                        border: 1.5px solid #D1D1D1;
                        background: #F9F9F9;
                        border-radius: 12px;
                        max-width: 130px;
                        display: flex;
                        input{
                            width: 40px;
                            border: 1px solid transparent;
                            border-right: 1px solid #D1D1D1;
                            background-color: transparent;
                        }
                        select{
                            background: #F9F9F9;
                            border: 1px solid transparent;
                        }
                    }
                }
            }
        }
        &-subtotal{
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            p{
                font-weight: 600;
                font-size: 12px;
            }
        }
        &-apply{
            display: flex;
            width: 100%;
            justify-content: space-between;
            align-items: flex-start;
            border-radius: 12px;
            background: #F9F9F9;
            margin:  40px 0px;
            input{
                padding: 12px 12px 12px 22px; 
                background-color: transparent;
                border: 1px solid transparent;
                width: 70%;
            }
            button{
                padding: 12px 12px 12px 22px;
                width: 30%; 
                font-weight: bold;
                background-color: transparent;
                font-size: 15px;
                border: 2px solid transparent;
                border-radius: 0px 12px 12px 0px;
                
            }
        }
        &-footer{
            display: flex;
            width: 100%;
            justify-content: space-between;
            align-items: flex-start;
            &-text{
                h3{
                    font-weight: 600;
                    font-size: 12px;
                }
                p{
                    font-weight: normal;
                    font-size: 12px;
                    color: #6A983C;
                }
            }
            &_price{
                font-weight: 600;
                font-size: 26px;
                color: #6A983C;
            }
        }
    }

}