.singleproduct{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: 60px;
    &-header{
        width : 100%;
        border-radius: 12px;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        height: 60vh;
        background: linear-gradient(0deg, #000000 0%, rgba(0, 0, 0, 0) 100%) ,url('../../assets/source/Singleproduct/food.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        padding: 35px;
        margin-bottom: 100px;
        &-inner{
            display: flex;
            justify-content: space-around;
            align-items: flex-start;
            &-author{
                display: flex;
                flex-direction: column;
                padding: 0 5px;
                margin-right: 35px;
                span{
                    display: inline-block;
                    color: #A9A9A9;
                    font-weight: 600;
                    font-size: 12px;
                    padding: 6px;
                }
                a, p{
                    font-weight: 600;
                    font-size: 12px;
                    color: #FFFFFF;
                    padding: 6px;
                    display: inline-block;
                    margin-bottom: 0;
                    &:hover{
                        color: #fff;
                    }
                }
            }
        }
        h1{
            font-weight: 600;
            font-size: 32px;
            color: #FFFFFF;
            max-width: 500px;
        }
    }
    &-body{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        width: 80%;
        margin-bottom: 50px;
        &-nav{
            width: 18%;
            h3{
                font-weight: 600;
                font-size: 18px;
                color: #151515;
            }
            &-tags{
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                justify-content: flex-start;
                span{
                    color: #6A983C;
                    background: #F4F8EC;
                    border-radius: 12px;
                    padding: 4px 10px;
                    margin-right: 15px;
                    margin-bottom: 15px;
                }
            }
            a{
                font-weight: bold;
                font-size: 15px;
                color: #151515;
                display: inline-block;
                padding: 6px 6px 6px 26px;
                background-image: url('../../assets/source/Singleproduct/back.svg');
                background-repeat: no-repeat;
                background-position: 0% center;
                background-size: 16px 16px contain;
                text-decoration: none;
                &:hover{
                    color: #151515;
                }
            }
        }
        &-text{
            width: 75%;
            p{
                font-family: Open Sans;
                font-weight: normal;
                font-size: 17px;
                margin-bottom: 50px;
            }
            h3{
                font-weight: 600;
                font-size: 18px;
            }
            &-share{
                display: flex;
                align-items: center;
                justify-content:space-between;
                margin-bottom: 100px;
                h3{
                    font-weight: 600;
                    font-size: 18px;
                }
                a{
                    font-weight: bold;
                    font-size: 15px;
                    line-height: 22px;
                    color: #151515;
                    background: #F5F5F5;
                    padding: 8px 12px 8px 36px;
                    border-radius: 12px;
                    &:hover{
                        color: black;
                        text-decoration: none;
                    }
                    &:nth-child(2){
                        background-image: url('../../assets/source/Singleproduct/Facebook.svg');
                        background-repeat: no-repeat;
                        background-position: 10% center;
                    }

                    &:nth-child(3){
                        background-image: url('../../assets/source/Singleproduct/Pinterest.svg');
                        background-repeat: no-repeat;
                        background-position: 10% center;
                    }

                    &:nth-child(4){
                        background-image: url('../../assets/source/Singleproduct/Twitter.svg');
                        background-repeat: no-repeat;
                        background-position: 10% center;
                    }

                    &:nth-child(5){
                        background-image: url('../../assets/source/Singleproduct/LinkedIn.svg');
                        background-repeat: no-repeat;
                        background-position: 10% center;
                    }
                }
            }
        }
        &-comment{
            h3{
                font-weight: 600;
                font-size: 22px;
            }
            p{
                font-weight: normal;
                font-size: 17px;
            }
            &-form{
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: flex-start;
                flex-wrap: wrap;
                margin-bottom: 50px;
                &-inputs{
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    width: 45%;
                    label{
                        font-weight: 600;
                        font-size: 12px;
                        margin-bottom: 5px;
                    }
                    input{
                        width: 100%;
                        background: #F9F9F9;
                        padding: 12px 16px 12px 22px;
                        border: 1px solid #D1D1D1;
                        border-radius: 12px;
                        margin-bottom: 25px;
                    }
                }
                &-checkbox{
                    display: flex;
                    align-items: center;
                    width: 100%;
                    background: #F9F9F9;
                    padding: 8px 16px 8px 22px;
                    border: 1px solid #D1D1D1;
                    border-radius: 12px;
                    margin-bottom: 40px;
                }
                &-textarea{
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    width: 100%;
                    margin-bottom: 35px;
                    label{
                        font-weight: 600;
                        font-size: 12px;
                        margin-bottom: 5px;
                    }
                    textarea{
                        width: 100%;
                        padding: 12px 22px;
                        background: #F9F9F9;
                        border: 1px solid #D1D1D1;
                        border-radius: 12px;
                        height: 150px;
                        resize: none;
                    }
                }
                button{
                    font-weight: bold;
                    font-size: 15px;
                    color: #FFFFFF;
                    background: #6A983C;
                    border: 2px solid #46760A;
                    border-radius: 12px;
                    padding: 12.5px 48px;
                }
            }
        }
    }

    &-suggestion{
        width: 100%;
        &-top{
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 50px;
            h3{
                font-weight: 600;
                font-size: 22px;
                color: #151515;
            }
            button{
                font-weight: bold;
                font-size: 15px;
                color: #151515;
                padding: 6px 36px 6px 12px;
                background-color: transparent;
                border: none;
                background-image: url('../../assets/source/greenright.svg');
                background-repeat: no-repeat;
                background-position: 90% center;
            }
        }
        &-components{
            display: flex;
            justify-content: space-between;
        }
    }
}




.feedback{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
    h1{
        font-weight: 600;
        font-size: 22px;
        text-align: left;
        width: 100%;
        margin-bottom: 25px;
    }
    &-card{
        background: #FFFFFF;
        border: 1px solid #D1D1D1;
        padding: 16px;
        border-radius: 12px;
        margin-bottom: 25px;
        width: 70%;
        &-author{
            display: flex;
            justify-content: flex-start;
            align-items: center;
            margin-bottom: 25px;
            img{
                width: 40px;
                height: 40px;
                border-radius: 50%;
                object-fit: cover;
            }
            &-person{
                span{
                    font-weight: 600;
                    font-size: 12px;
                    display: inline-block;
                    padding: 4px 12px;
                    
                    &:nth-child(2){
                        color: red;
                        padding: 4px 12px;
                        border-radius: 12px;
                        background: #F4F8EC;
                        font-weight: 600;
                        font-size: 12px;
                        color: #6A983C;
                    }
                }
                p{
                    font-weight: normal;
                    font-size: 12px;
                    color: #D1D1D1;
                    padding: 4px 12px;
                    margin-bottom: 0;
                }
            }
        }
        &:nth-child(2){
            width: 100%;
        }
    }
}